.genome-spy{display:flex;flex-direction:column;position:relative}.genome-spy .loading-message{position:absolute;top:0;bottom:0;left:0;right:0;display:flex;align-items:center;justify-content:center}.genome-spy .loading-message .message{color:#666;opacity:0;transition:opacity 0.7s}.genome-spy .track-stack{flex-grow:1;width:100%;display:flex;flex-direction:column;transform:scale(1, 1);opacity:1;transition:opacity 0.7s, transform 0.7s}.genome-spy.loading .track-stack{transform:scale(0.95, 0.95);opacity:0}.genome-spy.loading .loading-message .message{opacity:1}.genome-spy .viewport-overlay{position:absolute;top:0;bottom:0}.genome-spy .track,.genome-spy .axis-track,.genome-spy .sample-track{position:relative}.genome-spy .simple-track:not(:first-child){margin-top:4px}.genome-spy .tooltip{position:absolute;max-width:300px;overflow:hidden;background:#f6f6f6;padding:10px;font-size:13px;box-shadow:0px 3px 15px 0px rgba(0,0,0,0.21);pointer-events:none;z-index:100}.genome-spy .tooltip>.title{margin-bottom:10px}.genome-spy .tooltip table{margin-top:10px;border-collapse:collapse}.genome-spy .tooltip table:first-child{margin-top:0}.genome-spy .tooltip table th,.genome-spy .tooltip table td{padding:0.2em 0.4em;vertical-align:top}.genome-spy .tooltip table th:first-child,.genome-spy .tooltip table td:first-child{padding-left:0}.genome-spy .tooltip table th{text-align:left;font-weight:bold}.genome-spy .tooltip .color-legend{display:inline-block;width:0.8em;height:0.8em;margin-left:0.4em;box-shadow:0px 0px 3px 1px white}.genome-spy .gene-track-tooltip .summary{font-size:90%}.genome-spy .sample-track-sample-tooltip table td.color{width:1px}.genome-spy .message-box{display:flex;align-items:center;justify-content:center;position:absolute;top:0;height:100%;width:100%}.genome-spy .message-box>div{border:1px solid red;padding:10px;background:#fff0f0}.context-menu{position:fixed;padding:7px 0;background:#f6f6f6;border:#e0e0e0;border-radius:2px;z-index:100;box-shadow:0px 3px 15px 0px rgba(0,0,0,0.21);cursor:pointer}.context-menu .context-menu-item,.context-menu .context-menu-header{display:block;padding:0 15px;font-size:13px;line-height:20px;font-family:sans-serif}.context-menu .context-menu-header{font-size:12px;color:#777}.context-menu div.context-menu-item{color:#888}.context-menu a.context-menu-item:hover{background-color:#e0e0e0}.context-menu .context-menu-divider{margin:5px 0;border-top:1px solid #e0e0e0}
