.genome-spy-app{position:absolute;top:0;bottom:0;left:0;right:0;display:flex;flex-direction:column;font-family:sans-serif}.genome-spy-app .toolbar{height:38px;display:flex;flex-direction:row;align-items:center;background-color:#6c82ab;box-shadow:0 0 4px rgba(0,0,0,0.4);border-top:1px solid #adb9d0;border-bottom:1px solid #4a5d81}.genome-spy-app .toolbar .spacer{flex-grow:1}.genome-spy-app .toolbar .title{color:#fff;font-size:18px;font-weight:normal;text-shadow:0 1px 2px rgba(0,0,0,0.3)}.genome-spy-app .toolbar .vis-title{color:#fff;font-size:14px;font-weight:normal;text-shadow:0 1px 2px rgba(0,0,0,0.2)}.genome-spy-app .toolbar .search-input{background-color:#5c74a1;color:#fff;border:1px solid #536991;font-size:14px;width:260px;padding:3px 6px;position:relative}.genome-spy-app .toolbar .search-help{box-sizing:border-box;position:absolute;border:none;background:#f6f6f6;font-size:14px;padding:10px;z-index:1000;visibility:hidden;box-shadow:0px 3px 14px 0px rgba(0,0,0,0.15);opacity:0;transition:opacity 0.2s, transform 0.2s ease-in-out, visibility 0.2s;transform:translate(0, 10px)}.genome-spy-app .toolbar .search-help>:first-child{margin-top:0}.genome-spy-app .toolbar .search-help li{cursor:pointer}.genome-spy-app .toolbar .search:focus-within .search-help{opacity:1;visibility:visible;transform:translate(0, 0)}.genome-spy-app .toolbar>*{padding-left:10px}.genome-spy-app .toolbar>*:last-child{margin-right:10px}.genome-spy-app .toolbar>.tool-btn{padding:0;background-color:#7c90b4;background-image:linear-gradient(to bottom, #8698ba, #7288af);color:#fff;height:26px;width:30px;border:1px solid;border-top-color:#8c9ebe;border-left-color:#8395b8;border-right-color:#5c74a1;border-bottom-color:#4a5d81;box-shadow:0 1px 1px rgba(0,0,0,0.15);border-radius:2px;margin-left:10px;display:flex;align-items:center;justify-content:center}.genome-spy-app .toolbar>.tool-btn>svg{transform:scale(1.25)}.genome-spy-app .toolbar>.tool-btn:active{background-color:#536991}.genome-spy-app .toolbar>.tool-btn:active>*{position:relative;top:1px}.genome-spy-app .toolbar>.tool-btn:disabled{background-color:#6c82ab;color:#b6c1d5}.genome-spy-app .genome-spy-container{position:relative;flex-grow:1;margin:10px}
