body{position:absolute;margin:0;top:0;bottom:0;left:0;right:0;display:flex;flex-direction:column;font-family:Nunito, sans-serif;font-size:15px}header{position:relative;padding:10px;padding-bottom:0}header .toolbar{position:absolute;right:10px;top:10px;bottom:20px}header .buttons{display:flex;margin:0;transition:visibility 1s, opacity 1s, transform 1s}header .buttons.hidden{visibility:hidden;opacity:0;transform:translateX(30px)}header .buttons button{border:0;background:none;font-size:14px;border:2px solid #666;border-radius:4px}h1{margin:0;font-size:160%;border-bottom:2px solid black}h1 .main-title{position:relative}h1 .main-title .beta{position:absolute;font-size:13px;transform:translate(-1.6em, -0.4em) rotate(20deg)}h1 small{font-size:55%;margin-left:0.5em}h2{margin-bottom:0.4em;margin-top:1em}p{margin:0.3em 0}a{color:black}main{margin:10px;flex-grow:1;position:relative}main .drop-zone{border-radius:10px;border:4px solid transparent}main .drop-zone.dragging{background-color:#f4f4f4;border:4px dashed #ccc}main>div{position:absolute !important;left:0;right:0;top:0;bottom:0;visibility:hidden}main>div.active-panel{visibility:visible}.file-box{max-width:900px;margin-left:auto;margin-right:auto}footer{font-size:70%;padding:6px 10px}.upload-button-wrapper{margin:1em 0}.file-table{margin-top:1em;margin-left:1em;border-collapse:collapse}.file-table td{padding-right:0.6em}.file-table .status{text-align:center;padding-right:0.1em;width:1em}.file-table .okay{color:#5a2}.file-table .example-file{color:#bbb}.buttons{display:flex;align-items:center;justify-content:center;margin:2em 0}.btn{background-color:#548fcc;padding:8px 1em;color:#fff;border:0;border-radius:3px;font-weight:bold;font-size:13px;transition:color 0.2s, background-color 0.2s}.btn:disabled{background-color:#eee;color:#ccc}.btn:focus{outline:none;box-shadow:0 0 4px #0af}.btn:hover:not(:disabled):not(.active){background-color:#4887c8}.btn:active:not(:disabled):not(.active){box-shadow:0 0 4px #0af,inset 0 3px 3px rgba(0,0,0,0.2);background-color:#397abe}.btn.active{background-color:#3169a3;text-shadow:0 0 3px rgba(0,0,0,0.5)}.btn.btn-lg{transition:color 1s, background-color 1s;font-size:22px;border-radius:4px;padding:0.6em 1em}.btn-group{display:flex}.btn-group .btn:not(:first-child){border-top-left-radius:0;border-bottom-left-radius:0}.btn-group .btn:not(:last-child){border-top-right-radius:0;border-bottom-right-radius:0}.btn-group .btn:not(:last-child){border-right:1px solid #7ba9d8}.genome-spy .tooltip{max-width:initial}
